import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	data() { 
		return {
			value: 1,
			year: '<=5年',
			selectList: [{
					label: '21',
					value: '21'
				},
				{
					label: '22',
					value: '22'
				}, {
					label: '23',
					value: '23'
				}
			],
			gender: ['男性','女性','其他'],
			isInfect: ['有','无'],
			bladder: ['正常或高','低'],
			course: ['<=5年','>5年'],
			incontinence: ['有','无'],
			genderIndex: -1,
			isInfectIndex: -1,
			bladderIndex: -1,
			courseIndex: -1,
			incontinenceIndex: -1,
			themeColor: '#a69eff',
			leftTopText: '年龄',
			age: '',
			imgList:[
				'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1573461179335&di=743743eaa389cb83a9c0766e93f9e6d4&imgtype=0&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Farchive%2F6491c748440e49a77286875012fcdf98a4a419f0.jpg',
				'http://i.caigoubao.cc/619923/eks-7.jpg'
			],
		}
	},
	methods: {
		selectGender(e) {
			this.genderIndex = e;
		},
		selectInfect(e){
			this.isInfectIndex = e;
		},
		selectBladder(e){
			this.bladderIndex = e;
		},
		selectCourse(e){
			this.courseIndex = e; 
		},
		selectIncontinence(e){
			this.incontinenceIndex = e;
		},
		previewImg(e){
			let current = e.target.dataset.src;
			uni.previewImage({
				current: current,
				urls: this.imgList
			})
		},
		deleteImg(index){
			this.imgList.splice(index,1);
		},
		addImg(){
			uni.chooseImage({
				count:3-this.imgList.length,
				success:res=>{
					this.imgList.push(res.tempFilePaths)
				},
				fail: err=>{
					console.log(err);
				}
			})
		},
		open() {
			this.$refs.selector.show();
		},
		onConfirm(e) {
			this.age = e.result;
		} 
	},
	components: {
		wPicker
	}
}
